.payments {
    width: 100%;
    margin-bottom: 10px;
    white-space: nowrap;
    display: flex;
    @include xs {
        width: 100%; }
    &__link {
        height: 18px;
        flex-grow: 1;
        display: inline-block;
        text-indent: -999px;
        overflow: hidden;
        &_mastercard {
            width: 26px;
            background: url(../img/content/payments/mastercard.png) center  no-repeat;
            background-image: url(/img/content/payments/mastercard.svg); }
        &_visa {
            width: 36px;
            background: url(../img/content/payments/visa.png) center  no-repeat;
            background-image: url(/img/content/payments/visa.svg); }
        &_qiwi {
            width: 39px;
