            background-image: url(/img/content/payments/qiwi.svg); }
        &_paypal {
            width: 39px;
            background: url(../img/content/payments/paypal.png) center  no-repeat;
            background-image: url(/img/content/payments/paypal.svg); }
        &_webmoney {
            width: 14px;
            background: url(../img/content/payments/wm.png) center  no-repeat;
            background-image: url(/img/content/payments/wm.svg); }
        &_yandex {
            width: 14px;
            background: url(../img/content/payments/yandex.png) center  no-repeat;
            background-image: url(/img/content/payments/yandex.svg); }
        &_alfa {
            width: 13px;
