        &_megafon {
            width: 15px;
            background: url(../img/content/payments/megafon.png) center  no-repeat;
            background-image: url(/img/content/payments/megafon.svg); }
        &_tele2 {
            width: 26px;
            background: url(../img/content/payments/tele2.png) center  no-repeat;
