body {
   position: relative;
   min-width: 320px;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   color: $color1;
   font-size: 16px;
   height: 100%;
   line-height: 1;
   font-family: Arial, Helvetica, Verdana;
   overflow-x: hidden; }

html {
   overflow-x: hidden; }
.container {
   &.container-md {
      max-width: 1210px;
      padding: 0 5px; } }
.input {
   &_text {
      border: 1px solid $text;
      background-color: #fff;
      color: #000;
      @include placeholder-color($text); } }
.uppercase {
   text-transform: uppercase; }
.btn {
   border-radius: 3px 0 0 3px;
   display: inline-block;
   text-transform: uppercase;
   text-decoration: none;
   border: none;
   cursor: pointer;
   font-weight: bold;
   text-align: center;
   transition: background-color 0,3s ease 0s;
   &_black {
      background-color: $color1;
      color: #fff;
      &:hover {
         background-color: #000;
         color: #fff; } }
   &_green {
      background: $green;
      color: #fff;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0;
      transition: all 0.3s ease 0s;
      &:hover {
         background: #0b750b; } }
   &__arrow {
      width: 40px;
      height: 40px;
      background: url(../img/header/my_books_arrow_down.svg) no-repeat center;
      border-left: 1px solid #3EA73E;
      &:hover {
         background-color: $green2; } }
   &_user-books {
      height: 40px; }
   &__wrapper {
      position: relative;
      overflow: hidden;
      display: flex;
      align-content: center; }
   &__content {
      border-radius: 3px 0 0 3px;
      overflow: hidden;
      display: flex;
      position: relative;
      align-items: center;
      text-align: center;
      color: #fff;
      white-space: nowrap;
      padding-right: 9px;
      padding-left: 40px;
      height: 100%;
      &:hover {
         background-color: $green2; }
      &::before {
            content: '';
            background: url(../img/header_sprite.png) no-repeat -170px -60px;
            background-image: url(../img/header_sprite.svg);
            display: block;
            left: 15px;
            top: 50%;
            transform: translateY(-50%);
            height: 17px;
            width: 20px;
            position: absolute; } }
   &.coolbtn {
      height: 50px;
      margin:  0;
      width: 100%;
      font-size: 16px;
      @include xs {
         font-size: 12px; } } }




.dropdown-content {
   display: none;
   position: absolute;
   background-color: #f1f1f1;
   min-width: 160px;
   z-index: 1; }
.book_icons {
   bottom: 0;
   right: 15px;
   text-align: right;
   position: absolute;
   > span {
      margin: 0 12px 12px 0;
      vertical-align: bottom;
      font-weight: bold; } }
.icon_adult {
   width: 32px;
   height: 32px;
   color: #000;
   background: #fff;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 14px;
   border: 2px solid #fff;
   text-align: center;
   white-space: nowrap;
   box-shadow: inset 0 0 0 1px #000;
   border-radius: 50%; }
.age {
   line-height: 0;
   height: 29px;
   display: inline-block; }
.digit1 {
   background: url(../img/content/1.png) center center no-repeat;
   background-image: url(/img/content/1.svg);
   width: 7px; }
.digit2 {
   background: url(../img/content/6.png) center center no-repeat;
   background-image: url(img/content/6.svg);
   width: 8px;
   margin-top: 1px;
   height: 28px; }

.sticker {
   position: absolute; }
.flags {
   left: -4px;
   top: 20px;
   margin-top: -3px;
   z-index: 1;
   max-width: 90%;
   a {
      transition: opacity ease-out 0.5s;
      opacity: 1;
      text-transform: uppercase;
      color: #fff;
      font-weight: bold;
      text-decoration: none;
      font-size: 14px;
      font-family: Arial,sans-serif;
      line-height: 39px;
      position: relative;
      padding-left: 10px;
      box-shadow: 0 1px 0  0 rgba(0, 0, 0, 0.2);
      margin-bottom: 7px;
      min-width: 142px;
      height: 36px;
      margin-top: 3px;
      display: block;
      &::before {
         content: "";
         display: block;
         position: absolute;
         left: 0;
         bottom: -4px;
         width: 0;
         height: 0;
         border-style: solid;
         border-width: 0 4px 4px 0; }
      &::after {
         content: '';
         display: block;
         position: absolute;
         top: 0;
         right: -7px;
         box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.2); } } }
.flag_best {
   background-color: $accent;
   &::after {
      border-top: 18px solid $accent;
      border-bottom: 18px solid $accent;
      border-right: 7px solid transparent; }
   .flag_icon {
      background-position: -10px -11px; } }
.flag_hit {
   background-color: #09B3CB;
   &:hover {
      .sticker a {
         opacity: 1; } }
   &::after {
      border-top: 18px solid #09B3CB;
      border-bottom: 18px solid #09B3CB;
      border-right: 7px solid transparent; }
   .flag_icon {
      background-position: -10px -61px; }
   &:before {
      border-color: transparent #a93a0a transparent transparent; } }
.flag_icon {
   background-image: url(../img/content/flag_icons.svg);
   display: inline-block;
   width: 20px;
   margin: 0 10px 0 0;
   height: 36px;
   line-height: 36px;
   vertical-align: top; }
.flag_text {
   display: inline-block;
   overflow: hidden;
   text-overflow: ellipsis;
   max-width: 100%;
   padding-right: 10px;
   height: 36px;
   line-height: 36px;
   vertical-align: top; }

.type_audio {
      &::before {
         content: '';
         background: url(../img/content/type1.png) no-repeat center center;
         background-image: url(../img/content/type1.svg);
         width: 16px;
         height: 16px; } }
.type_hardcopy {
   &::before {
         background: url(../img/content/type12.png) no-repeat center center;
         background-image: url(../img/content/type12.svg);
         width: 16px;
         height: 16px; } }
.type::before {
   content: '';
   position: absolute;
   top: 7px;
   left: 0; }
.action-read {
   border-radius: 3px; }

.devider {
   width: 100%;
   height: 1px;
   background-color: #DCDBDC;
   margin-top: 12px;
   margin-bottom: 17px; }

.multi-factor__link {
   color: #767579;
   text-decoration: none;
   font-style: normal;
   position: relative;
   padding-left: 22px;
   display: inline-block;
   margin-left: 10px;
   &:hover {
      color:#767579 {} }
   &::before {
      position: absolute;
      content: '';
      display: block;
      background-image: url(../img/content/pen.svg);
      height: 15px;
      width: 16px;
      left: 1px;
      top: 2px; } }








