@media only screen and (max-width: 767px) {
  .footer ul li {
    text-align: center;
  }
}

.footer ul li .footer__header a {
  display: block;
  padding: 2px 6px 15px;
  font-size: 14px;
  line-height: 16px;
  font-weight: bold;
}

.footer_soc_icons {
  list-style-type: none;
}

.footer_soc_icons li div {
  display: flex;
  flex-wrap: wrap;
}

@media only screen and (max-width: 1023px) {
  .footer_soc_icons li div {
    justify-content: center;
  }
}

.footer_soc_icon {
  display: inline-block;
  width: 24px;
  height: 24px;
  background: url(../img/content/social/soc-icons.svg);
  margin-right: 12px;
  margin-bottom: 13px;
}

.footer_soc_icon_vk {
  background-position: 0 -24px;
}

.footer_soc_icon_ok {
  background-position: -24px -24px;
}

.footer_soc_icon_fb {
  background-position: -48px -24px;
}

.footer_soc_icon_ins {
  background-position: -72px -24px;
}

.footer_soc_icon_tw {
  background-position: -96px -24px;
}

.footer_soc_icon_mail {
  background-position: -144px -24px;
}

.footer_soc_icon_dzen {
  background-position: -168px -24px;
}

.footer_soc_icon_tg {
  background-position: -192px -24px;
}
